
'use client';
import { useState, useEffect } from 'react';
import Link from 'next/link';
import { useAuth } from '@/contexts/AuthContext';
import { db } from '@/lib/firebase'; // <-- CORREGIDO
import { collection, getDocs, doc, updateDoc } from 'firebase/firestore';
import StarRating from '@/app/components/StarRating';

const ClientesPage = () => {
  const { user, userRole } = useAuth();
  const [clientes, setClientes] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchClientes = async () => {
      if (userRole !== 'admin' || !user) return;
      try {
        const clientesCollection = collection(db, 'users'); // <-- CORREGIDO
        const clientesSnapshot = await getDocs(clientesCollection);
        const clientesData = clientesSnapshot.docs
          .map(doc => ({ id: doc.id, ...doc.data() }))
          .filter(cliente => cliente.id !== user.uid);
        setClientes(clientesData);
      } catch (error) {
        console.error("Error al obtener los clientes: ", error);
      }
      setLoading(false);
    };
    fetchClientes();
  }, [user, userRole]);

  const handleRatingChange = async (clientId, newRating) => {
    try {
      const clientRef = doc(db, 'users', clientId); // <-- CORREGIDO
      await updateDoc(clientRef, { rating: newRating });
      setClientes(clientes.map(c => c.id === clientId ? { ...c, rating: newRating } : c));
    } catch (error) {
      console.error("Error al actualizar la calificación: ", error);
    }
  };

  if (userRole !== 'admin') {
    return <p className="text-red-500 text-center mt-8">No tienes permiso para ver esta página.</p>;
  }

  if (loading) {
    return <p className="text-center mt-8">Cargando clientes...</p>;
  }

  return (
    <div className="container mx-auto px-4 py-8">
      <h1 className="text-3xl font-bold mb-8">Clientes y Mascotas</h1>
      <div className="bg-white shadow-md rounded my-6 overflow-x-auto">
        <table className="min-w-full leading-normal">
          <thead>
            <tr>
              <th className="px-5 py-3 border-b-2 border-gray-200 bg-gray-100 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">Nombre</th>
              <th className="px-5 py-3 border-b-2 border-gray-200 bg-gray-100 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">Email</th>
              <th className="px-5 py-3 border-b-2 border-gray-200 bg-gray-100 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">Calificación</th>
              <th className="px-5 py-3 border-b-2 border-ray-200 bg-gray-100 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">Mascotas</th>
              <th className="px-5 py-3 border-b-2 border-gray-200 bg-gray-100"></th>
            </tr>
          </thead>
          <tbody>
            {clientes.map(cliente => (
              <tr key={cliente.id}>
                <td className="px-5 py-5 border-b border-gray-200 bg-white text-sm">
                  <p className="text-gray-900 whitespace-no-wrap">{cliente.displayName || 'No especificado'}</p>
                </td>
                <td className="px-5 py-5 border-b border-gray-200 bg-white text-sm">
                  <p className="text-gray-900 whitespace-no-wrap">{cliente.email}</p>
                </td>
                <td className="px-5 py-5 border-b border-gray-200 bg-white text-sm">
                  <StarRating 
                    clientId={cliente.id} 
                    initialRating={cliente.rating || 0} 
                    onRatingChange={handleRatingChange} 
                  />
                </td>
                <td className="px-5 py-5 border-b border-gray-200 bg-white text-sm">
                  <p className="text-gray-900 whitespace-no-wrap">0</p>
                </td>
                <td className="px-5 py-5 border-b border-gray-200 bg-white text-sm text-right">
                  <Link href={`/admin/clientes/${cliente.id}`} className="text-indigo-600 hover:text-indigo-900 font-semibold">
                    Ver Detalles
                  </Link>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default ClientesPage;
